import Link from 'next/link';
import Nav from './Nav';

const Header = () => {
  return (
    <header>
      <Link href="/">
        <a>
          <h2>
            <span>M</span>edical <span>M</span>alpractice
          </h2>
        </a>
      </Link>

      <Nav />

      <style jsx>{`
        header {
          display: flex;
          flex-flow: wrap;
          background-color: #000000;
          border-bottom: 2px solid #9b9bb0;
        }
        a {
          flex: 1;
          color: #dddddd;
          text-decoration: none;
          transition: all 0.2s;
        }
        a:hover {
          color: #bbbbbb;
        }
        h2 {
          font-size: 19px;
          white-space: nowrap;
          margin: 7px 5px 6px;
          text-align: center;
          text-transform: uppercase;
        }
        h2 > span {
          color: #ff726f;
        }
      `}</style>
    </header>
  );
};

export default Header;
